<template>
  <!--
    	luoyiming
    	2019-10-24
    	-&
    -->
  <div class="product">
    <!--&-->
    <div class="common-level-rail d-b-c">
      <el-button size="small" type="primary" @click="addClick" icon="el-icon-plus">&</el-button>
      <el-form :inline="true" :model="formSearch" size="small">
        <el-form-item>
          <el-checkbox v-model="formSearch.is_menu" @change="changeIsMenuFunc"></el-checkbox>
          <el-checkbox v-model="formSearch.pack_up" @change="changePackUpFunc"></el-checkbox>
        </el-form-item>
      </el-form>
    </div>
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <div>
          <el-table
            size="small"
            :data="tableData"
            style="width: 100%;margin-bottom: 20px;"
            row-key="access_id"
            border
            default-expand-all
            ref="theTable"
            :tree-props="{ children: 'children' }"
            v-loading="loading"
          >
            <el-table-column prop="name" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.path=='/plus'" class="fb red f18">
                  {{scope.row.name}}
                </span>
                <span v-else>
                  {{scope.row.name}}
                </span>
              </template>
            </el-table-column>
            <el-table-column prop="path" label=""></el-table-column>
            <el-table-column prop="is_route" label="" width="90">
              <template slot-scope="scope">
                <span v-if="scope.row.is_route==1"></span>
                <span v-if="scope.row.is_route==0"></span>
                <span v-if="scope.row.is_route==2"></span>
              </template>
            </el-table-column>
            <el-table-column prop="is_show" label="" width="80">
              <template slot-scope="scope">
                <el-switch
                  v-model="scope.row.is_show"
                  :active-value="1"
                  :inactive-value="0"
                  @change="isShowFunc(scope.row)"
                  active-color="#13ce66"
                  inactive-color="#cccccc"
                ></el-switch>
              </template>
            </el-table-column>
            <el-table-column prop="sort" label="" width="60"></el-table-column>
            <el-table-column prop="create_time" label="" width="140"></el-table-column>
            <el-table-column prop="name" label="" width="230">
              <template slot-scope="scope">
                <el-button @click="addClick(scope.row,'copy')" type="text" size="small" v-if="scope.row.path!='/plus'"></el-button>
                <el-button @click="addClick(scope.row,'child')" type="text" size="small"></el-button>
                <el-button @click="editClick(scope.row)" type="text" size="small"></el-button>
                <el-button @click="deleteClick(scope.row)" type="text" size="small" v-if="scope.row.path!='/plus'"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>

    <!---->
    <Add v-if="open_add" :open_add="open_add" :add_type="add_type" :rawData="rawData" :selectModel="selectModel" @closeDialog="closeDialogFunc($event, 'add')"></Add>

    <!---->
    <Edit v-if="open_edit" :open_edit="open_edit" :rawData="rawData" :selectModel="selectModel" @closeDialog="closeDialogFunc($event, 'edit')"></Edit>

  </div>
</template>
<script>
import AccessApi from '@/api/access.js';
import Edit from './part/Edit.vue';
import Add from './part/Add';;
import { deepClone } from '@/utils/base.js';
export default {
  components: {
    /**/
    Edit: Edit,
    Add: Add
  },
  data() {
    return {
      /**/
      loading: true,
      /**/
      formSearch: {
        /**/
        is_menu: false,
        /**/
        pack_up: false
      },
      /**/
      rawData: [],
      /**/
      tableData: [],
      /**/
      open_add: false,
      /**/
      add_type:'',
      /**/
      selectModel:{},
      /**/
      open_edit: false,
      /**/
      userModel: {},
    };
  },
  created() {
    /**/
    this.getTableList();
  },
  methods: {
    /**/
    changeIsMenuFunc(e) {
      let list = deepClone(this.rawData);
      if (e) {
        this.showScreen(list, 1);
        this.tableData = list;
      } else {
        this.tableData = list;
      }
    },

    /**/
    isShowFunc(e) {
      let self = this;
      AccessApi.status({ access_id: e.access_id, status: e.is_show }, true)
        .then(data => {
          if (data.code == 1) {
            self.$message({
              message: data.msg,
              type: 'success'
            });
            self.getTableList();
          }
        })
        .catch(error => {
          self.loading = false;
        });
    },

    /**/
    changePackUpFunc(e) {
      this.forArr(this.tableData, !e);
    },

    /**/
    forArr(arr, isExpand) {
      arr.forEach(i => {
        this.$refs.theTable.toggleRowExpansion(i, isExpand);
        if (i.children) {
          this.forArr(i.children, isExpand);
        }
      });
    },

    /**/
    changeShowFunc(e) {
      let list = deepClone(this.rawData);
      if (e == 'all') {
        this.tableData = list;
      } else {
        let type;
        if (e == 'show') {
          type = 1;
        }
        if (e == 'hide') {
          type = 0;
        }
        this.showScreen(list, type);
        this.tableData = list;
      }
    },

    /**/
    showScreen(list, type) {
      for (let i = 0; i < list.length; i++) {
        let item = list[i];
        if (typeof item.is_menu != 'undefined' && item.is_menu != type) {
          list.splice(i, 1);
          i--;
        } else {
          if (item.children.length > 0) {
            this.showScreen(item.children, type);
          }
        }
      }
    },

    /**/
    getTableList() {
      let self = this;
      let Params = {};
      AccessApi.accessList(Params, true)
        .then(res => {
          self.loading = false;
          self.rawData = res.data;
          self.tableData = res.data;
        })
        .catch(error => {
          self.loading = false;
        });
    },

    /**/
    addClick(e,type) {
      if(e&&typeof(e)!='undefined'){
        this.add_type=type;
        this.selectModel=deepClone(e);
      }else{
        this.parents_id=0;
      }
      this.open_add = true;
    },

    /**/
    editClick(item) {
      this.selectModel = item;
      this.open_edit = true;
    },

    closeDialogFunc(e, f) {
      if (f == 'add') {
        this.open_add = e.openDialog;
        if (e.type == 'success') {
          this.getTableList();
        }
      }
      if (f == 'edit') {
        this.open_edit = e.openDialog;
        if (e.type == 'success') {
          this.getTableList();
        }
      }
    },

    /**/
    deleteClick(row) {
      let self = this;
      self
        .$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(() => {
          self.loading = true;
          AccessApi.delAccess(
            {
              access_id: row.access_id
            },
            true
          )
            .then(data => {
              if (data.code == 1) {
                self.loading = false;
                self.$message({
                  message: data.msg,
                  type: 'success'
                });
                self.getTableList();
              }
            })
            .catch(error => {
              self.loading = false;
            });
        })
        .catch(() => {});
    }
  }
};
</script>



