<template>
  <!--
    	luoyiming
    	2020-06-20
    	diy--
    -->
    <div>
        <div class="common-form">
            <span>{{ curItem.name }}</span>
        </div>
        <el-form size="small" :model="curItem" label-width="100px">
            <!--  -->
            <el-form-item label="">
              <el-select v-model="curItem.params.auto.category">
                <el-option label="" :value="0"></el-option>
                  <el-option
                    v-for="item in category"
                    :key="item.category_id"
                    :label="item.name"
                    :value="item.category_id">
                  </el-option>
              </el-select>
            </el-form-item>
            <!--  -->
            <el-form-item label="">
                <el-input type="number" style="width: auto;" v-model="curItem.params.auto.showNum"
                          class="w-auto"></el-input>
                <div>
                    
                    <a href="#" target="_blank"> - </a>
                    
                </div>
            </el-form-item>
            <!---->
            <el-form-item label="">
              <el-radio-group v-model="curItem.style.display">
                <el-radio :label="10"></el-radio>
                <el-radio :label="20"></el-radio>
              </el-radio-group>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import ArticleApi from '@/api/article.js';
    export default {
        data() {
            return {
                category: {},
            };
        },
        props: ['curItem', 'selectedIndex', 'opts'],
        created() {
            /**/
            this.getData();
        },
        methods: {
            /**/
            getData() {
                let self = this;
                ArticleApi.getCategory({
                    page_id: self.page_id
                }, true).then(data =>
                {
                    self.category = data.data.catgory;
                })
                    .catch(error =>
                    {
                        self.loading = false;
                    });
            },
        }
    };
</script>

<style></style>



