<template>
  <!--
      	luoyiming
      	2020-06-01
      	--
      -->
  <div>
    <div class="common-level-rail"><el-button size="small" type="primary" icon="el-icon-plus" @click="addArticle"></el-button></div>
    <div class="table-wrap">
      <el-table :data="tableData" style="width: 100%" v-loading="loading">
        <el-table-column prop="article_id" label="ID" width="60"></el-table-column>
        <el-table-column prop="address" label="" width="50">
          <template slot-scope="scope">
            <img v-img-url="'image.file_path',scope.row" width="30" height="30" />
          </template>
        </el-table-column>
        <el-table-column prop="article_title" label="">
          <template slot-scope="scope">
            <div class="text-ellipsis" :title="scope.row.article_title">{{scope.row.article_title}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="category.name" label="" width="120"></el-table-column>
        <el-table-column prop="virtual_views" label="" width="100"></el-table-column>
        <el-table-column prop="article_sort" label="" width="100"></el-table-column>
        <el-table-column prop="article_status" label="" width="100">
          <template slot-scope="scope">
            <span v-if="scope.row.article_status == 1" class="green"></span>
            <span v-if="scope.row.article_status == 0" class="gray"></span>
          </template>
        </el-table-column>
        <el-table-column prop="create_time" label="" width="140"></el-table-column>
        <el-table-column prop="update_time" label="" width="140"></el-table-column>
        <el-table-column prop="name" label="" width="90">
          <template slot-scope="scope">
            <el-button @click="editArticle(scope.row)" type="text" size="small"></el-button>
            <el-button @click="deleteArticle(scope.row)" type="text" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <div class="pagination">
        <el-pagination background :current-page="curPage" :page-size="pageSize" layout="total, prev, pager, next, jumper" :total="totalDataNumber"></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import ArticleApi from '@/api/article.js';
export default {
  components: {},
  data() {
    return {
      /**/
      categoryData: [],
      /**/
      tableData: [],
      /**/
      open_add: false,
      /**/
      open_edit: false,
      /**/
      userModel: {},
      commentData: [],
      loading: true,
      /**/
      pageSize: 20,
      /**/
      totalDataNumber: 0,
      /**/
      curPage: 1
    };
  },
  created() {
    /**/
    this.getTableList();
  },
  methods: {

    /**/
    getTableList() {
      let self = this;
      let Params = {};
      Params.page = self.curPage;
      ArticleApi.articlelist(Params, true)
        .then(data => {
          self.loading = false;
          self.tableData = data.data.list.data;
          self.totalDataNumber = data.data.list.total;
        })
        .catch(error => {
          self.loading = false;
        });
    },

    /**/
    addArticle() {
      this.$router.push({
        path: '/operate/article/article/add'
      });
    },

    /**/
    editArticle(row) {
      let params = row.article_id;
      this.$router.push({
        path: '/operate/article/article/edit',
        query: {
          article_id: params
        }
      });
    },

    /**/
    handleCurrentChange(val) {
      let self = this;
      self.curPage = val;
      self.loading = true;
      self.getData();
    },

    /**/
    handleSizeChange(val) {
      this.curPage = 1;
      this.getData();
    },

    /**/
    deleteArticle(row) {
      let self = this;
      self
        .$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(() => {
          self.loading = true;
          ArticleApi.deleteArticle(
            {
              article_id: row.article_id
            },
            true
          )
            .then(data => {
              self.$message({
                message: data.msg,
                type: 'success'
              });
              self.loading = false;
              self.getTableList();
            })
            .catch(error => {});
        })
        .catch(() => {});
    },

    /**/
    deleteCategory(row) {
      let self = this;
      self
        .$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(() => {
          ArticleApi.deleteCategory(
            {
              category_id: row.category_id
            },
            true
          )
            .then(data => {
              self.$message({
                message: data.msg,
                type: 'success'
              });
              self.getTableList();
            })
            .catch(error => {});
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
    },
    handleClick(tab, event) {},

    /**/
    addCategory() {
      this.open_add = true;
    },

    /**/
    editCategory(item) {
      this.userModel = item;
      this.open_edit = true;
    },

    /**/
    closeDialogFunc(e, f) {
      if (f == 'add') {
        this.open_add = e.openDialog;
        if (e.type == 'success') {
          this.getTableList();
        }
      }
      if (f == 'edit') {
        this.open_edit = e.openDialog;
        if (e.type == 'success') {
          this.getTableList();
        }
      }
    }
  }
};
</script>

<style></style>



