<template>
  <!--
      	luoyiming
      	2020-06-01
      	--
      -->
    <div>
        <div class="common-level-rail">
            <el-button size="small" type="primary" @click="addCategory"></el-button>
        </div>
        <div class="table-wrap">
            <el-table :data="categoryData" style="width: 100%" v-loading="loading">
                <el-table-column prop="category_id" label="ID" width="180"></el-table-column>
                <el-table-column prop="name" label="	"></el-table-column>
                <el-table-column prop="sort" label=""></el-table-column>
                <el-table-column prop="create_time" label="" width="180"></el-table-column>
                <el-table-column prop="name" label="" width="150">
                    <template slot-scope="scope">
                        <el-button @click="editCategory(scope.row)" type="text" size="small"></el-button>
                        <el-button @click="deleteCategory(scope.row)" type="text" size="small"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!---->
            <Add v-if="open_add" :open_add="open_add" @closeDialog="closeDialogFunc($event, 'add')"></Add>

            <!---->
            <Edit v-if="open_edit" :open_edit="open_edit" :form="userModel"
                  @closeDialog="closeDialogFunc($event, 'edit')"></Edit>

        </div>
    </div>
</template>

<script>
    import ArticleApi from '@/api/article.js';
    import Edit from './part/edit.vue';
    import Add from './part/add.vue';
    import {deepClone} from '@/utils/base.js';
    export default {
        components: {
            /**/
            Edit,
            Add
        },
        data() {
            return {
                /**/
                categoryData: [],
                /**/
                open_add: false,
                /**/
                open_edit: false,
                /**/
                userModel: {},
                commentData: [],
                /**/
                loading: true,
            }
        },
        created() {
            /**/
            this.getTableList();

        },
        methods: {
            /**/
            getTableList() {
                let self = this;
                let Params = {};
                ArticleApi.getCategory(Params, true)
                    .then(data =>
                    {
                        self.loading = false;
                        self.categoryData = data.data.catgory;
                    })
                    .catch(error =>
                    {

                    });
            },


            /**/
            deleteCategory(row) {
                let self = this;
                self.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() =>
                {
                    self.loading = true;
                    ArticleApi.deleteCategory({
                        category_id: row.category_id
                    }, true)
                        .then(data =>
                        {
                            self.loading = false;
                            self.$message({
                                message: data.msg,
                                type: 'success'
                            });
                            self.getTableList();

                        })
                        .catch(error =>
                        {
                            self.loading = false;
                        });

                }).catch(() =>
                {

                });
            },
            handleClick(tab, event) {

            },

            /**/
            addCategory() {
                this.open_add = true;
            },

            /**/
            editCategory(item) {
//                this.userModel = item;
                this.userModel = deepClone(item);
                this.open_edit = true;
            },

            /**/
            closeDialogFunc(e, f) {
                if (f == 'add') {
                    this.open_add = e.openDialog;
                    if (e.type == 'success') {
                        this.getTableList();
                    }
                }
                if (f == 'edit') {
                    this.open_edit = e.openDialog;
                    if (e.type == 'success') {
                        this.getTableList();
                    }
                }
            },
        }
    }
</script>

<style>
</style>



