<template>
  <!--
      luoyiming
      2020-06-01
      --
    -->
  <div class="product-add pb50" v-loading="loading">
    <!---->
     <el-form size="small" :model="form" ref="form" :rules="rules" label-width="100px">
       <div class="common-form"></div>
       <el-form-item label="" prop="article_title"><el-input v-model="form.article_title" placeholder="" class="max-w460"></el-input></el-form-item>
       <el-form-item label="" prop="dec"><el-input v-model="form.dec" placeholder="" class="max-w460"></el-input></el-form-item>
       <el-form-item label="">
         <div>
           <el-button type="primary" @click="openUpload"></el-button>
           <img class="mt10" v-img-url="'image.file_path',form"  width="120px;" alt="" />
           <!---->
           <Upload v-if="isupload" :isupload="isupload" @returnImgs="returnImgsFunc"></Upload>
         </div>
       </el-form-item>
       <el-form-item label="">
         <el-select v-model="form.category_id" placeholder="">
           <el-option v-for="(item, index) in category" :key="index" :label="item.name" :value="item.category_id"></el-option>
         </el-select>
       </el-form-item>
       <el-form-item label=""><el-input type="number" v-model="form.virtual_views" placeholder="" class="max-w460"></el-input></el-form-item>
       <el-form-item label="">
         <el-radio-group v-model="form.article_status">
           <el-radio :label="1"></el-radio>
           <el-radio :label="0"></el-radio>
         </el-radio-group>
       </el-form-item>
       <el-form-item label="">
         <div class="edit_container">
           <Uediter v-if="!loading" :text="ueditor.text" :config="ueditor.config" ref="ue"></Uediter>
         </div>
       </el-form-item>
       <el-form-item label=""><el-input type="number" v-model="form.article_sort" placeholder="" class="max-w460"></el-input></el-form-item>
       <!---->
       <div class="common-button-wrapper">
         <el-button size="small" type="info" @click="cancelFunc" :loading="loading"></el-button>
         <el-button size="small" type="primary" @click="onSubmit" :loading="loading"></el-button>
       </div>
     </el-form>
  </div>
</template>

<script>
import ArticleApi from '@/api/article.js';
import Uediter from '@/components/UE.vue';
import Upload from '@/components/file/Upload';
export default {
  components: {
    /**/
    Uediter,
    /**/
    Upload: Upload
  },
  data() {
    return {
      /**/
      loading: true,
      /**/
      isupload: false,
      /**/
      ueditor: {
        text: '',
        config: {
          initialFrameWidth: 400,
          initialFrameHeight: 500
        }
      },
      /*form*/
      form: {
        article_title: '',
        category_id: '',
        image_id: '',
        image:{},
        article_sort: 1,
        article_status: '0',
        virtual_views: 1,
        article_content: ''
      },
      /**/
      category: [],
      /**/
      rules: {
        article_title: [{ required: true, message: '', trigger: 'blur' }],
        dec: [{ required: true, message: '', trigger: 'blur' }]
      }
    };
  },
  created() {

    this.getDetail();

  },

  methods: {

    /**/
    openUpload() {
      this.isupload = true;
    },

    getContent: function() {
      return this.$refs.ue.getUEContent();
    },

    /**/
    returnImgsFunc(e) {
      let self = this;
      if (e != null) {
        this.form.image_id = e[0].file_id;
        this.form.image.file_path = e[0].file_path;
      }
      this.isupload = false;
    },

    getDetail() {
      let self = this;
      // 
      const params = self.$route.query.article_id;
      ArticleApi.toEditArticle({article_id: params},true).then(res => {
          self.ueditor.text = res.data.model.article_content;
          self.form = res.data.model;
          if(!self.form.image){
            self.form.image={};
          }
          self.category = res.data.catgory;
          self.loading = false;
        })
        .catch(error => {});
    },

    /**/
    onSubmit() {
      let self = this;
      let params = this.form;
      self.form.article_content = this.$refs.ue.getUEContent();
      // 
      ArticleApi.editArticle(params, true)
        .then(data => {
          self.$message({
            message: data.msg,
            type: 'success'
          });
          self.$router.push('/operate/article/index');
        })
        .catch(error => {});
    },

    /**/
    cancelFunc() {
      this.$router.push({
        path: '/operate/article/index'
      });
    }
  }
};
</script>

<style>
.edit_container {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.ql-editor {
  height: 400px;
}
</style>



