<template>
  <!--
    	luoyiming
    	2020-06-3
    	-&
    -->
  <el-dialog title="&" :visible.sync="dialogVisible" @close="dialogFormVisible" :close-on-click-modal="false" :close-on-press-escape="false">
    <el-form size="small" :model="formData" :rules="formRules" ref="form">
      <!---->
      <el-form-item label="" prop="name" :label-width="formLabelWidth">
        <el-input v-model="formData.name" autocomplete="off" placeholder=""></el-input>
      </el-form-item>
      <!---->
      <el-form-item label="" prop="name" :label-width="formLabelWidth">
        <el-radio-group v-model="formData.is_route">
          <el-radio :label="1"></el-radio>
          <el-radio :label="0"></el-radio>
          <el-radio :label="2"></el-radio>
        </el-radio-group>
      </el-form-item>
      <!---->
      <el-form-item label="" prop="parent_id" :label-width="formLabelWidth">
        <el-cascader size="small" v-model="parentsVal" :options="accessList" :props="propsParam" @change="handleChange"></el-cascader>
      </el-form-item>
      <!---->
      <el-form-item label="" prop="path" :label-width="formLabelWidth">
        <el-input v-model="formData.path" autocomplete="off" placeholder="" :disabled="formData.path=='/plus'"></el-input>
        <p>index/index</p>
      </el-form-item>
      <!---->
      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="formData.icon" autocomplete="off" placeholder="icon"></el-input>
        <p></p>
      </el-form-item>
      <!---->
      <el-form-item label="" :label-width="formLabelWidth" v-if="formData.is_route==1">
        <el-radio-group v-model="formData.is_menu">
          <el-radio :label="1"></el-radio>
          <el-radio :label="0"></el-radio>
        </el-radio-group>
      </el-form-item>
      <!---->
      <el-form-item label="" :label-width="formLabelWidth">
        <el-radio-group v-model="formData.is_show">
          <el-radio :label="1"></el-radio>
          <el-radio :label="0"></el-radio>
        </el-radio-group>
      </el-form-item>
      <!---->
      <el-form-item label="" :label-width="formLabelWidth" v-if="formData.is_route == 1">
        <el-input v-model="formData.redirect_name" autocomplete="off" placeholder=""></el-input>
      </el-form-item>
      <!---->
      <el-form-item label="" prop="sort" :label-width="formLabelWidth">
        <el-input v-model="formData.remark" placeholder="" type="textarea"></el-input>
      </el-form-item>
      <!---->
      <el-form-item label="" prop="sort" :label-width="formLabelWidth"><el-input v-model="formData.sort" placeholder="" type="number"></el-input></el-form-item>
    </el-form>
    <!---->
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogFormVisible"> </el-button>
      <el-button type="primary" @click="onSubmit()" :disabled="loading"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import AccessApi from '@/api/access.js';
import { deepClone } from '@/utils/base.js';
export default {
  data() {
    return {
      /**/
      loading: false,
      /*form*/
      formData: {
        /**/
        name: '',
        /**/
        path:'',
        /**/
        views:'',
        /**/
        alias:'',
        /**/
        icon:'',
        /**/
        is_menu: 1,
        /**/
        is_route: 1,
        /**/
        is_show: 0,
        /**/
        sort: 1,
        /*ID*/
        parent_id: 0
      },
      /**/
      formRules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        path: [{ required: true, message: '', trigger: 'blur' }],
        views: [{ required: true, message: '', trigger: 'blur' }],
        alias: [{ required: true, message: '', trigger: 'blur' }]
      },
      /*ID*/
      parentsVal: [],
      /**/
      accessList: [],
      /**/
      srot: '1',
      /**/
      formLabelWidth: '120px',
      /**/
      dialogVisible: false,
      /**/
      propsParam: {
        label: 'name',
        value: 'access_id',
        checkStrictly: true
      }
    };
  },
  props: {
    open_edit:Boolean,
    add_type:String,
    rawData:Array,
    selectModel:Object
  },
  created() {
    this.dialogVisible = this.open_edit;
    this.accessList = deepClone(this.rawData);
    this.accessList.unshift({name:'',access_id:0})
    this.formData=deepClone(this.selectModel);
    this.findParentsID(this.accessList);

  },
  methods: {

    /**/
    handleChange(e) {
    },

    /*id*/
    findParentsID(list){
      let flag=false;
      for(let i=0;i<list.length;i++){
        let item=list[i];
        if(item.access_id==this.formData.parent_id){
          this.parentsVal.unshift(item.access_id);
          flag=true;
          break;
        }else{
          let children=item.children;
          if(typeof children!='undefined'&&children.length>0){
              if(this.findParentsID(children)){
                this.parentsVal.unshift(item.access_id);
                flag=true;
                break;
              }
          }
        }
      }
      return flag;
    },

    /**/
    onSubmit() {
      let self = this;
      let params = self.formData;
      if(self.parentsVal.length>0){
        params.parent_id=self.parentsVal[self.parentsVal.length-1];
      }
      self.$refs.form.validate(valid => {
        if (valid) {
          self.loading = true;
          AccessApi.editAccess(params, true)
            .then(res => {
              if (res.code == 1) {
                self.$message({
                  message: res.msg,
                  type: 'success',
                });
                self.$emit('closeDialog', {
                  type: 'success',
                  openDialog: false,
                  data:params
                });
                self.loading = false;
              }
            })
            .catch(error => {
              self.loading = false;
            });
        }
      });
    },

    /**/
    dialogFormVisible(e) {
      this.$emit('closeDialog', {
        type: 'error',
        openDialog: false
      });
    }
  }
};
</script>

<style></style>


