<template>
  <div class="article-box">

    <!---->
    <div class="common-seach-wrap">
      <el-form size="small" :inline="true" class="demo-form-inline">
        <el-form-item>
          <el-button size="small" icon="el-icon-notebook-2" @click="chooseList"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <!---->
    <div class="article-content">
      <div class="table-wrap">
        <el-table size="mini" :data="tableData" border style="width: 100%">
          <el-table-column prop="article_title" label=""></el-table-column>
          <el-table-column prop="category.name" label="" width="80"></el-table-column>
          <el-table-column label="" width="80">
            <template slot-scope="scope">
              <el-button size="mini" @click="changeFunc(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!---->
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          background
          :current-page="curPage"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="totalDataNumber"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import ArticleApi from '@/api/article.js';
export default {
  data() {
    return {
      /*tab*/
      activeTab: 'second',
      /**/
      pageSize: 5,
      /**/
      totalDataNumber: 0,
      /**/
      curPage: 1,
      /**/
      loading: true,
      /**/
      tableData: [],
      /**/
      activePage: {}
    };
  },
  created() {

    this.chooseList();

    /**/
    this.getData();
  },
  watch: {},
  methods: {
    /**/
    handleCurrentChange(val) {
      let self = this;
      self.curPage = val;
      self.getData();
    },

    /**/
    handleSizeChange(val) {
      this.pageSize = val;
      this.curPage = 1;
      this.getData();
    },

    /**/
    getData() {
      let self = this;
      self.loading = true;
      let Params = {};
      Params.page = self.curPage;
      Params.list_rows = self.pageSize;
      ArticleApi.articlelist(Params, true)
        .then(data => {
          self.loading = false;
          self.tableData = data.data.list.data;
          self.totalDataNumber = data.data.list.total;
        })
        .catch(error => {
          console.log(error);
        });
    },

    /**/
    changeFunc(e) {
      let obj={};
      obj.name = e.article_title;
      obj.url = 'pages/article/detail/detail?article_id=' + e.article_id;
      obj.type = '';
      this.$emit('changeData',obj);
    },

    /**/
    chooseList(){
      let obj={};
      obj.name = '';
      obj.url = 'pages/article/list/list';
      obj.type = '';
      this.$emit('changeData',obj);
    }

  }
};
</script>

<style></style>



